self.addEventListener('install', function (event) {
  event.waitUntil(
    caches.open('weaver').then(function (cache) {
      return cache.addAll([
        '/css/bootstrap.css',
        '/css/main.css',
        '/js/bootstrap.min.js',
        '/js/jquery.min.js',
        '/offline.html',
      ]);
    })
  );
});
